
import { useStore } from "vuex";
import { defineComponent, reactive, ref } from "vue";
import type { ElForm } from "element-plus";

import rules from "../config/account-config";
import cache from "utils/localCache";

export default defineComponent({
  setup() {
    const store = useStore();
    const account = reactive({
      name: cache.get("name") || "",
      password: cache.get("password") || "",
    });
    const formRef = ref<InstanceType<typeof ElForm>>();

    const loginByAccount = (payload: any, skipCheck = false) => {
      if (skipCheck) {
        store.dispatch("login/accountLoginAction", {
          name: "coderwhy",
          password: "123456",
        });
        return;
      }
      formRef.value?.validate((valid) => {
        if (valid) {
          //1.存储登录信息到本地
          if (payload.isKeepPassword) {
            cache.set("name", account.name);
            cache.set("password", account.password);
            cache.set("isKeepPassword", true);
          } else {
            cache.delete("name");
            cache.delete("password");
            cache.delete("isKeepPassword");
          }

          //2.登录验证
          store.dispatch("login/accountLoginAction", { ...account });
        }
      });
    };

    return {
      account,
      rules,
      loginByAccount,
      formRef,
    };
  },
});
